import React from 'react';
import axios from 'axios';
import team from '../team.jpg';
class Student extends React.Component{
   
        state = {
            firstName : '',
            lastName  : '',
            place     : ''
        }
    
    handleChange= (e) =>{
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    handleSubmit= () =>{
       
        if(this.state.firstName !=='' && this.state.lastName !=='' && this.state.place !==''){
         
            axios.post('http://localhost:5000/students',this.state)

            .then(res =>{
                console.log('sucessfully added');
                this.setState({firstName :'', lastName :'', place:''})
            })
           
            .catch(err =>{
                console.log('thers is an error');
            })
            window.location = '/';
        }
    }
    render()
    {
        return(
            <div className='row text-center'>
                <div className="col-md-4">
                    <form onSubmit={()=>this.handleSubmit()}>
                        <input required onChange={(e)=>this.handleChange(e)} name='firstName' value={this.state.firstName} style={{borderRadius:'10px', fontFamily:'cursive, sans-serif',marginLeft :'50px', marginTop : '20px'}} type="text" placeholder="firstName" className='form-control'/>
                        <input required onChange={(e)=>this.handleChange(e)} name='lastName' value={this.state.lastName} style={{borderRadius:'10px',fontFamily:'cursive, sans-serif',marginLeft :'50px', marginTop : '20px'}} type="text" placeholder="lastName" className='form-control'/>
                        <input required onChange={(e)=>this.handleChange(e)} name='place' value={this.state.place} style={{borderRadius:'10px',fontFamily:'cursive, sans-serif',marginLeft :'50px', marginTop : '20px'}} type="text" placeholder="place" className='form-control'/>
                        <button  style={{borderRadius:'10px',fontFamily:'cursive, sans-serif',width:'320px', alignContent:'center' ,marginLeft :'65px', marginTop : '20px'}} className="btn btn-primary" type="submit">CREATE</button>
                    </form>  
                </div>
                <div className="col-md-8">
                    <img src={team} alt="team"/>
                </div>
            </div>
        )
    }
}

export default Student;